.App {  
  font-family: inherit;   
}
.loginbg{
  background-image: url("./images/pc-bg.jpg");
  background-attachment: fixed;
  background-color:#eee;
  background-repeat: no-repeat;  
  text-align: center;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  display: flex;
  min-height: 100vh;
}
.frm-label{
  font-size: 16px;
}
form{
  background: #00000059;
  padding: 20px;
  border-radius: 5px;
}
input {
  display: block;
  min-width: 90%;
  margin: 15px;
  padding: 15px;
  width: 40em; 
  border-style: none;
  border: 1px solid #bc668d;
  transition: 0.1s ease;
}

input:hover {
  border: 1px solid #bc668d; 
}
button {
  margin: 0.8em;
  padding: 10px;
  border: 1px solid #e4e6e8; 
  cursor: pointer;
  transition: 0.1s ease-in;
}
button:hover {
  background-color: #bc668d;
  color: white;
}

.bg-dark {
  background-color: #ecc0c9!important; 
  background: linear-gradient(to right, rgb(254, 254, 255), rgb(202, 132, 153));
  justify-content: space-around;
  display: flex;  
  width: 100%;
  z-index: 1;
}

.navbar-dark .navbar-nav .nav-item {
  color: #fff;
  align-self: center;
  padding-right: 20px;
}
.footer {
  font-family: inherit;   
  background-color: #bc668d!important;
  background: linear-gradient(to right, rgb(254, 254, 255), rgb(202, 132, 153));
  padding: 10px;
  color: #fff;
  text-align: center;
}
.product-card{
  /* border: 1px solid #eee; */
  text-align: center;
}
.product-detail{
    font-family: inherit;   
    background: #eee;   
    padding: 0;
    padding: 5px 10px;    
}
.img-size{
  width: 100%;
  height: 400px;
}
.add-cart-btn{
  width: 100%;
  margin: 0px;
  border-top: 1px solid #d1d1d1;
  font-weight: 500;
}

.cart-bg{
  display: flex;
  padding: 20px;
  background: #f5f5f5;
  justify-content: space-between;
  align-items: center;
}

.cart-img{
  width: 200px;
  height: 200px;
}

.continue-link{
  padding-bottom: 15px;
  color: #333;
}